.TodoItem {
	background-color: rgb(0, 0, 0);
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 24px;
	box-shadow: 0 5px 5px rgb(255, 0, 0);
	border-radius: 12px;
}
.TodoItem-p {
	padding: 5px;
	margin: 24px 0 24px 24px;
	width: calc(100% - 120px);
	font-size: 18px;
	line-height: 24px;
	font-weight: 400;
}
.TodoItem-p--complete {
	text-decoration: line-through;
}
